.Skills {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 500px;
    margin: 2.25em auto;
}

.Skills-header {
    display: block;
    font-family: 'Oswald', sans-serif;
    font-size: 48px;
    margin-top: 0;
}

.Skills-icons {
    width: 100%;
    height: 100%;
    margin: 0 auto;
}

.Skills-icons svg {
    max-height: 105px;
    fill: currentColor;
}

.Skill-icons svg path {
    fill: inherit;
}

.Skills-copy {
    width: 100%;
    padding: 0 auto;
    text-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12)
}

.Skills-icon {
    width: 17%;
    margin: 1em auto;
    display: inline-block;
    fill: currentColor;
}